const {h} = require(`@cycle/dom`)
const switchPath = require(`switch-path`)
const {filterLinks} = require(`cycle-history`)
const latestObj = require(`rx-combine-latest-obj`)
const {getUrl} = require(`utils`)
const routes = require(`./routes`)
const {Navbar, Sidebar, Content} = require(`./partials`)
const styles = require(`./main.styl`)

function getRouteValue(location) {
  const {value} = switchPath(location.pathname, routes)
  return value
}

const intent = ({ DOM }) => ({

  click$: DOM
    .select(`.${styles.link}`)
    .events(`click`)
    .merge(
      DOM
        .select(`.${styles.link}`)
        .events(`touchstart`)
    ).filter(filterLinks),

  scroll$: DOM
    .select(`.${styles.content}`)
    .events(`scroll`),

  hover$: DOM
    .select(`.${styles.sidebar}`)
    .events(`mouseenter`)
    .merge(
      DOM
        .select(`.${styles.sidebar}`)
        .events(`mouseleave`)
    ),

})

const model = ({
  click$,
  scroll$,
  hover$,
},{
  History,
}) => latestObj({

  url: click$
    .map(getUrl)
    .startWith(History.value.pathname),

  isHovered: hover$
    .map(e => {
      if (e.type === `mouseenter`) {
        return true
      }
      return false
    })
    .startWith(false)
    .distinctUntilChanged(),

  isScrolled: scroll$
    .map(e => {
      return e.target.scrollTop > 200
    })
    .startWith(false)
    .distinctUntilChanged(),

  routeValue: History
    .map(getRouteValue)
    .startWith(null)
    .pairwise()
    .distinctUntilChanged(),
}).distinctUntilChanged()

const view = state => state
  .map(
    ({
      isHovered,
      isScrolled,
      routeValue,
    }) => h(`div.${styles.app}`, {key: 0}, [
        Sidebar(isHovered),
        h(`div.${styles.contentContainer}`, [
          Navbar(isScrolled),
          Content(routeValue),
        ]),
      ])
  ).distinctUntilChanged()

const Main = responses => {
  const actions = intent(responses)
  const state = model(actions, responses)
  const view$ = view(state)

  return {
    DOM: view$,
    History: state.url,
  }
}

export {Main}



