const {h} = require(`@cycle/dom`)
const {GSAP} = require(`../../../utils/gsap-widget`)
const styles = require(`../main.styl`)

function validateValue([zero, one]) {
  if (!zero || zero === one) {
    return ``
  }
  return zero
}

const enterOptions = {
  time: 1,
  from: {
    css: {
      position: `absolute`,
      opacity: 0,
      transform: `translate3d(30em, 0, 0) rotateZ(100deg) scale(0.1)`,
    },
  },
  to: {
    css: {
      opacity: 1,
      transform: `translate3d(0,0,0) rotateZ(0) scale(1)`,
    },
  },
}

const exitOptions = {
  time: 1,
  from: {
    css: {
      position: `absolute`,
      opacity: 1,
      transform: `translate3d(0, 0, 0) rotateX(0) scale(1)`,
    },
  },
  to: {
    opacity: 0,
    transform: `translate3d(10em, 5em, -1000px) rotateX(90deg) scale(0.1)`,
  },
}

const content = routeValue => h(`div`, {className: styles.content, key: 2}, [
  h(`div`, {style: {position: `relative`}, key: 3 }, [
    GSAP(exitOptions, [
      validateValue(routeValue),
    ]),
    GSAP(enterOptions, [
      routeValue[1],
    ]),
  ]),
  h(`div`, {style: {height: `500rem`}, key: 4}, []),
])

export default content



