const {h} = require(`@cycle/dom`)
const styles = require(`../main.styl`)

const sidebar = isHovered => h(`div`,
  {
    className: `${isHovered ? styles.pinned : styles.sidebar}`,
  },
  [
    h(`div`, {className: styles.menuIcon}, []),
  ])

export default sidebar



